import style from './Colors.module.css';

import ShowColor from './co/showColor.js'

function CircleList(props) {
  let colors = props.colors
  console.log(props)
  let colorsNode = colors.map((str, index)=>
    <ShowColor color={str} key={index}></ShowColor>
  )
  return colorsNode
}


