function ShowColor(props) {
  let mycolor = useRef(null)
  let [value, setValue] = useState(props.color)
  useEffect(()=>{
    mycolor.current.style['background-color'] = value
  },[value])
  function handleChange(e) {
    setValue(e.target.value)
    // mycolor.current.style['background-color'] = e.target.value
  }
  return (
    <div className={style.App}>
      <div className={style.circle} ref={mycolor}></div>
      <input 
      type="text" 
      // ref={this.mycolor} 
      onChange={handleChange.bind(this)} 
      value={value} 
      className={style.input}/>
    </div>
  )
}


export default ShowColor

